<!-- 首页 -->
<template>
	<div ref="tableBox" class="table-box">
		<el-table ref="table" :data="tableData" :height="tableH" class="center" size="mini" style="text-align:center;width:100%;">
			<el-table-column width="80"  type="selection" height="40px"></el-table-column>
			<el-table-column prop="carNum" width="200" label="车牌号"></el-table-column>
			<el-table-column prop="lineState" width="200" label="车辆在线状态"></el-table-column>
			<el-table-column prop="fleet" width="200" label="所属车队"></el-table-column>
			<el-table-column prop="carType" width="200" label="车辆类型"></el-table-column>
			<el-table-column prop="goodsID" width="200" label="关联设备ID"></el-table-column>
			<el-table-column prop="goodsType" width="200" label="关联设备类型"></el-table-column>
		</el-table>
		<div ref="pagination" class="block">
			<el-pagination class="pagination" :page-size="20" @current-change="changeCurPage" layout="prev, pager, next" :total="total"></el-pagination>
		</div>
	</div>	
</template>

<script>
var ParentFn = ()=>import('../../conmon/js/commo.js');
var Parent;
export default {
	name: 'homePage',
	data() {
		return {
			tableData: [
			{
				id:'1',
				carNum: '粤B12345',
				lineState:'在线',
				fleet: '宇宙第一车队',
				carType: '宇宙飞船',
				goodsID: '15245632',
				goodsType: '无敌型'
			},
			{
				id:'2',
				carNum: '粤B12345',
				lineState:'在线',
				fleet: '宇宙第一车队',
				carType: '宇宙飞船',
				goodsID: '15245632',
				goodsType: '无敌型'
			},
			{
				id:'3',
				carNum: '粤B12345',
				lineState:'在线',
				fleet: '宇宙第一车队',
				carType: '宇宙飞船',
				goodsID: '15245632',
				goodsType: '无敌型'
			},
			{
				id:'4',
				carNum: '粤B12345',
				lineState:'在线',
				fleet: '宇宙第一车队',
				carType: '宇宙飞船',
				goodsID: '15245632',
				goodsType: '无敌型'
			}
			],
			total: 0,
			tableH: 0

		}
	},
	created() {
		this.total = this.tableData.length;
		ParentFn().then(value =>{
			Parent = new value.default.ParentFn();
			
		})
	},
	mounted() {
		var tbaleBoxH = this.$refs.tableBox.offsetHeight,
		paginationH = this.$refs.pagination.offsetHeight;
		this.tableH = tbaleBoxH - paginationH;
	},
	methods: {
		changeCurPage(e){

		}
	}
}
</script>

<style scoped>
.table-box{padding: 10px 5px 0;flex: 1;display: flex;flex-direction: column;}
.block{display: flex;justify-content: center;padding: 40px 0 20px;}
</style>

